.atbds_customCheckbox input[type="checkbox"] {
  display: none;
}

.atbds_customCheckbox label {
  font-size: 15px;
  color: #868eae;
  display: inline-block !important;
  font-size: 14px;
}

.atbds_customCheckbox input[type="checkbox"] + label {
  min-width: 20px;
  min-height: 20px;
  font-size: 14px;
  display: flex;
  flex-flow: column;
  position: relative;
  padding-left: 38px;
  margin-bottom: 0;
  line-height: 1.4;
  font-weight: 400;
  color: #868eae;
}

.atbds_customCheckbox input[type="checkbox"] + label:after {
  position: absolute;
  left: 0;
  top: 0;
  width: 18px;
  height: 18px;
  border-radius: 3px;
  content: "";
  background-color: #fff;
  border-width: 1px;
  border-style: solid;
  border: 1px solid #c6d0dc;
  transition: 0.3s ease;
}

.atbds_customCheckbox input[type="checkbox"] + label:before {
  position: absolute;
  font-size: 12px;
  left: 4px;
  top: 2px;
  font-weight: 900;
  content: "\f00c";
  font-family: "Font Awesome 5 Free";
  display: block;
  opacity: 0;
  transition: all 0.3s ease 0s;
  z-index: 2;
  color: #3e62f5;
}

.atbds_customCheckbox input[type="checkbox"]:checked + label:after {
  background-color: #00bc5e;
  border: 1px solid #00bc5e;
}

.atbds_customCheckbox input[type="checkbox"]:checked + label:before {
  opacity: 1;
  color: #fff;
}

