.directorist_builder__content .directorist_builder__content--right .directorist_builder--tabContent .directorist_table tbody tr td .directorist_listing-actions .directorist_listing-type-checkbox {
  display: inline-block;
  margin: 5px 5px 5px 15px;
}

.directorist_custom-checkbox input {
  display: none;
}

.directorist_custom-checkbox input[type=checkbox] + label {
  min-width: 18px;
  min-height: 18px;
  font-size: 14px;
  display: flex;
  flex-flow: column;
  position: relative;
  padding-left: 28px;
  padding-top: 3px;
  margin-bottom: 0;
  line-height: 1.2;
  font-weight: 400;
  color: #5A5F7D;
}

.directorist_custom-checkbox input[type=checkbox] + label:before {
  position: absolute;
  font-size: 10px;
  left: 6px;
  top: 5px;
  font-weight: 900;
  font-family: 'Font Awesome 5 Free';
  content: "\f00c";
  display: block;
  opacity: 0;
  transition: all .3s ease 0s;
  z-index: 2;
}

.directorist_custom-checkbox input[type=checkbox] + label:after {
  position: absolute;
  left: 0;
  top: 0;
  width: 18px;
  height: 18px;
  border-radius: 50%;
  content: "";
  background-color: #fff;
  border: 2px solid #C6D0DC;
}

.directorist_custom-checkbox input[type=checkbox]:checked + label:after {
  background-color: #00B158;
  border-color: #00B158;
}

.directorist_custom-checkbox input[type=checkbox]:checked + label:before {
  opacity: 1;
  color: #fff;
}

.directorist_builder__content .directorist_badge {
  display: inline-block;
  padding: .25em .4em;
  font-size: 75%;
  font-weight: 700;
  line-height: 1.5;
  text-align: center;
  white-space: nowrap;
  vertical-align: baseline;
  border-radius: .25rem;
  margin-left: 6px;
  border: 0 none;
}

.directorist_builder__content .directorist_badge.directorist_badge-primary {
  color: #fff;
  background-color: #3E62F5;
}

.directorist_table-responsive {
  display: block !important;
  width: 100%;
  overflow-x: auto;
  overflow-y: visible;
}
